import React from "react"
import { graphql } from "gatsby"
import styled from "@emotion/styled"
import Layout from "../components/layout"
import SEO from "../components/seo"

const Content = styled.div`
  margin: 0 auto;
  max-width: 860px;
  padding: 1.45rem 1.0875rem;
`
const FooterText = styled.div`
  text-align: center;
`
const EmailOptin = styled.div`
  text-align: center;
  margin: 10px;
  border: 0.1em #677CE2 solid;
`

const MarkedHeader = styled.h1`
  display: inline;
  color: #677CE2;
  border-radius: 1em 0 1em 0;
  background-image: linear-gradient(
    -100deg,
    rgba(255, 250, 150, 0.15),
    rgba(255, 250, 150, 0.8) 100%,
    rgba(255, 250, 150, 0.25)
  );
`

const HeaderDate = styled.h5`
  margin-top: 10px;
  color: #606060;
`

const BlueColor = styled.css`
color: #677CE2;
`

const BlueColorLink = styled.a`
color: #677CE2;
text-decoration:none;
`

// STYLE THE TAGS INSIDE THE MARKDOWN HERE
const MarkdownContent = styled.div`
  a {
    text-decoration: none;
    position: relative;

    background-image: linear-gradient(
      rgba(255, 250, 150, 0.8),
      rgba(255, 250, 150, 0.8)
    );
    background-repeat: no-repeat;
    background-size: 100% 0.2em;
    background-position: 0 88%;
    transition: background-size 0.25s ease-in;
    &:hover {
      background-size: 100% 88%;
    }
  }
`

export default ({ data }) => {
  const post = data.markdownRemark
  return (
    <Layout>
      <SEO
        title={post.frontmatter.title}
        description={post.frontmatter.description || post.excerpt}
      />
      <Content>
        <MarkedHeader>{post.frontmatter.title}</MarkedHeader>
        <HeaderDate>
          {post.frontmatter.date} - <BlueColor>{post.fields.readingTime.text}</BlueColor>
        </HeaderDate>
        <MarkdownContent dangerouslySetInnerHTML={{ __html: post.html }} />
      </Content>
      {/* Add code here for email optin */}
      <footer>
        <hr/>
        <FooterText>Like what I write? Let's connect on <BlueColorLink href="https://twitter.com/intent/follow?screen_name=vaibhavthevedi">Twitter</BlueColorLink></FooterText>
        <EmailOptin><iframe src="https://vaibhavthevedi.substack.com/embed" height="250" width="480" frameBorder={0} scrolling="no"></iframe></EmailOptin>
      </footer>
    </Layout>
  )
}

export const pageQuery = graphql`
  query($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      excerpt(pruneLength: 160)
      frontmatter {
        date(formatString: "DD MMMM, YYYY")
        path
        title
      }
      fields {
        readingTime {
          text
        }
      }
    }
  }
`
